import tips from '../../../element/tips.vue'	
export default{
	components:{
		tips
	},
	props:{
		takeshop:Array
	},
	data() {
		return {
			
		}
	},
	
	methods:{
		takEout(id){
			wx.navigateTo({
				url:'../takeout/takeout?id=' + id
			})
		},
		onTips(bull,tips){
			// 页面渲染完后进行调用
			this.$nextTick(()=>{
				this.$refs.tip.init(bull,tips)
			})
		}
	},
	computed:{
		count(){
			if(this.takeshop.length<=0){
				this.onTips(true,"没有商品")
			}else{
				this.onTips(false,"")
			}
		}
	}
}
